import xinput from '../../../wxcomponents/common/xinput.vue'
import xlistInput from '../../../wxcomponents/xlist/xlist-input.vue'
export default {
	data() {
		return {
			buttonTitle: '登录',
			phonenum: '',
			scode: '',
			xpassword: true,
			xtype: 'number'
		}
	},
	methods: {
		onNumber(e) {
			this.phonenum = e.inputValue
		},
		onCode(e) {
			this.scode = e.inputValue
		},
		onNext() {
			this.bindValidate()
		},
		bindValidate(){
			let phonenum = this.phonenum.toString()
			let scode = this.scode.toString()
			uni.request({
				url: 'https://www.finetwm.com/xmRepair/shopInfo/login ',
				method: 'POST',
				header:{
					"content-Type": "application/x-www-form-urlencoded"
				},
				data: {
					phone: phonenum,
					password: scode
				},
				success: res => {
					if(res.data.data === '1'){
						uni.showToast({
							title: '登录成功'
						})
						setTimeout(()=>{
							uni.navigateTo({
								url: '../index/index'
							})
						},1000)
					} else{
						uni.showToast({
							title: '手机号码或密码错误',
							icon:'none'
						})
					}
				},
				fail: err => {
					console.log(err)
				}
			})
		}
	},
	components: {
		xinput,
		xlistInput
	}
};
